.section1 {
	background: url(../img/section1/bg-sec1.jpg) no-repeat center;
	overflow: hidden;
	.inner {
		// min-height: 1010px
		padding-top: 44px;
		padding-bottom: 194px;
		z-index: 1;
		position: relative;
		&:before {
			content: '';
			display: block;
			position: absolute;
			width: ((723 / 1170) * 100) * "1%";
			height: ((488 / 1170) * 100) * "1%";
			left: ((109 / 1170) * 100) * "1%"; // 109px
			bottom: ((-20 / 1170) * 100) * "1%";
			background: url(../img/section1/product-main__shadow.png) no-repeat center;
			opacity: 0.49;
			z-index: -1; } } }
.prices {
	@include hide-text;
	display: inline-block;
	vertical-align: bottom;
	width: (104 / 215) * 100 * "1%";
	bottom: -20px;
	position: relative; }
.desc {
	@include bebas-bold;
	@include fluid-font(24px, 30px, 900px, 1140px, 30px);
	line-height: 1;
	max-width: (94 / 215) * 100 * "1%";
	display: inline-block;
	vertical-align: bottom;
	margin: 0 0 12px 10px; }
.content {
	width: ((475 / 1140) * 100) * "1%";
	float: right;
	&__body {
		max-width: 420px;
		padding-left: 15px; } }
.section2 {
	background: url(../img/section2/bg-sec2.png) no-repeat center top;
	overflow: hidden;
	margin-top: -84px;
	z-index: 10;
	@include r(1100) {
		margin-top: -100px; }
	.inner {
		min-height: 1095px;
		padding-top: 140px;
		padding-bottom: 140px;
		z-index: 2;
		@include clr; } }
.section3 {
	background: url(../img/section3/bg-sec3.png) no-repeat center top;
	background-size: cover;
	overflow: hidden;
	margin-top: -120px;
	.inner {
		min-height: 1078px;
		padding-top: 120px; } }
.section4 {
	background-color: #FFF;
	margin-top: -14px;
	.inner {
		min-height: 1236px;
		padding-top: 129px;
		z-index: 2; } }
.section5 {
	margin-top: -256px;
	min-height: 1173px;
	background: url(../img/section5/bg-sec5.png) no-repeat center bottom;
	background-size: cover;
	overflow: hidden;
	z-index: 1;
	.inner {
		padding-top: 295px; } }
.section6 {
	min-height: 1138px;
	margin-top: -91px;
	background: url(../img/section6/bg-sec6.jpg) no-repeat center;
	background-size: cover;
	.inner {
		z-index: 1; } }
.section {
	position: relative;
	background-size: cover; }
