.liberty {
	position: relative;
	font-size: 0;
	margin-top: -18px;
	&:before {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		height: 154px;
		background: url(../img/section4/line.png) no-repeat center;
		background-size: contain; }
	&__list {
		padding: 100px 58px 0; }
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 50%;
		min-height: 130px;
		margin-bottom: 55px;
		padding-left: 156px;
		position: relative; }
	&__pic {
		position: absolute;
		top: 0;
		left: 0;
		width: 130px;
		height: 130px;
		overflow: hidden; }
	&__content {
		color: #2e2e2e;
		font-size: 30px;
		line-height: 1.2;
		padding: 10px 0;
		letter-spacing: -0.01em; }
	&__desc {
		color: #959595;
		font-size: 26px;
		text-align: center; }
	.btn {
		display: table;
		margin: 60px auto 0; } }
