[class^="btn"] {
	display: inline-block;
	vertical-align: top;
	text-align: center;
	// text-transform: uppercase
	cursor: pointer;
	text-decoration: none; }
.btn-primary {
	padding: 5.1% 7%;
	width: 100%;
	background-color: #ffd200;
	border: 4px solid #ffd200;
	border-radius: 5px;
	box-shadow: 0 12px 18px rgba(0, 0, 0, 0.32);
	position: relative;
	transition: all 0.3s;
	@include evo;
	span {
		position: relative;
		z-index: 2;
		text-shadow: 0 2px 0 #ffff00;
		color: #514718;
		transition: color 0.3s;
		@include fluid-font(26px, 36px, 900px, 1140px); }
	&:hover {
		box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11);
		border-color: #ffffff;
		background-color: transparent;
		span {
			color: #fff;
			text-shadow: 0 1px 1px rgba(0, 0, 0, 0.11); } } }
.btn-secondary {
	padding: 7px 67px;
	border: 5px solid #fff;
	border-radius: 33px;
	color: #fff;
	@include bebas-bold;
	font-size: 30px;
	transition: background-color 0.3s, border-color 0.3s;
	&:hover {
		background-color: #fcd000;
		border-color: #fcd000; } }
.btn {
	padding: 14px 126px;
	background-color: #fff;
	border: 5px solid #ff7200;
	border-radius: 44px;
	color: #ff7200;
	@include bebas-bold;
	font-size: 36px;
	transition: background-color 0.3s, color 0.3s;
	&:hover {
		background-color: #FF7200;
		color: #fafafc; } }
