.form {
	width: 300px;
	display: inline-block;
	vertical-align: top;
	position: relative;
	z-index: 2;
	&__content {
		padding: 40px 32px;
		border-radius: 10px;
		border: 5px solid #fff;
		background-color: transparent;
		text-align: center;
		font-size: 30px; }
	&__row {
		+ .form__row {
			margin-top: 14px; } }
	&__title {
		line-height: 1;
		margin-bottom: 32px; }
	&__btn {
		width: 100%;
		padding: 12px 16px;
		margin-top: 11px; } }
.input {
	display: block;
	width: 100%;
	padding: 13px 17px;
	border-radius: 10px;
	border: 2px solid #fff;
	background-color: transparent;
	@include f(bebas-reg, 24, 1, null);
	color: #fff;
	@include placeholder-color(#fff); }
